import service from '../../service.js';
export default {
	data() {
		return {
			type:'',
			ds_money:'',
			verifycode:'',
			countdown: 62,
			num: '',
			psw: '',
			code: '',
			captcha: '',
			uniqid: '',
			price: '',
			money: '',
			id: '',
			verification: [
				{
					name: 'num',
					title: '请输入数量'
				},
				{
					name: 'psw',
					title: '请输入置换密码'
				},
				{
					name: 'code',
					title: '请输入验证码'
				}
			],

		}
	},
	// computed:{
	// 	money(){
	// 		 var AllPrice = this.num * this.price * 7          
	// 		return AllPrice.toFixed(2); //保留两位小数的字符串
	// 	},
	// 	dollars(){
	// 		var AllPrice = this.num * this.numberValue 
	// 		return AllPrice.toFixed(2); //保留两位小数的字符串
	// 	}
	// },
	methods: {
		//手機驗證碼獲取倒計時
		codetime: function(obj, num) {
			if (num < 10) {
				obj.countdown = "0" + num;
			} else {
				obj.countdown = num;
			}
			num--;
			if (num < 0) {
				obj.countdown = 61;
			} else {
				setTimeout(function() {
					obj.codetime(obj, num);
				}, 1000);
			}
		},
		send() {
			var self = this
			uni.request({
				url: service.api.index.verifycode, 
				method: 'GET',
				data: {
					'temp': 'sms_sell',
					
				},
				
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('token')
				},
				success: res => {
					// console.log(res)
					var res = res.data;
					if (res.code == 1) {
						uni.showToast({
							icon: 'none',
							title: res.info
						});
						self.codetime(self, 60);
					} else {
						uni.showToast({
							icon: 'none',
							title: res.info
						});
						return;
					}
				}
			});
			// this.codetime(this,60);
		},
		emailValue(){
			var self = this
			uni.request({
				url: service.api.reg.sendEmailCode, 
				method: 'GET',
				data: {
					'email': uni.getStorageSync("member").email,
				},
				success: res => {
					console.log(res)
					var res = res.data;
					if (res.code == 1) {
						uni.showToast({
							icon: 'none',
							title: res.info
						});
						self.codetime(self, 60);
					} else {
						uni.showToast({
							icon: 'none',
							title: res.info
						});
						return;
					}
				}
			});
		},
		jump(e) {
			uni.switchTab({
				url: e
			})
		},
		submit() {
			/*在这里使用some会报Vue 引入js对象错误 */
			let canNext = this.verification.some(item => {
				if (this[item.name] == '') { //从原型链里拿数据进行判断 "this指向Vue实例"  this[item.name] 相当于this.psw item中的name的属性值必须要在data中定义
					uni.showToast({
						icon: 'none',
						title: item.title
					})
				}
				return this[item.name] == ''
			})
			if (!canNext) {
				 // 这里发请求
				 
				this.captchas();
				uni.request({
				url:this.$service.api.center.claimPurchasingOrder,
				method:"POST",
				data:{
					num: this.num,
					password: this.psw,
					verifycode: this.code,
					uniqid: this.uniqid,
					id: this.id,
					type:this.type
				},
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('token')
				},
				success: (res) => {
					var res = res.data;
					if(res.code == 1){
						uni.showToast({
							icon: 'none',
							title: res.info
						});
						
						setTimeout(()=>{
							uni.navigateTo({
								url:'/pages/center/my_deal?type='+ 1
							});
						},600);
					}else{
						uni.showToast({
							icon: 'none',
							title: res.info,
							duration:2000
						});
					}
				},
				complete:(res)=>{
					var res = res.data;
					// console.log(res,'complete');
					uni.showToast({
						icon: 'none',
						title: res.info,
						duration:2000
					});
				}
			});
				/* this.$service.auth(this, this.$service.api.center.claimPurchasingOrder, "POST", {
					num: this.num,
					password: this.psw,
					verifycode: this.code,
					uniqid: this.uniqid,
					id: this.id
				}, (self, res) => {
					// console.log(res)
					uni.showToast({
						icon: 'none',
						title: res.info
					});

					setTimeout(() => {
						uni.navigateTo({
							url: '/pages/center/my_deal'
						})
					}, 1500);
				}); */
			}
			
			/*------------*/
			
			/*------------*/
			
		},
		captchas() { //图形验证
			/* this.$service.auth(this, this.$service.api.home.captcha, "GET", {
				// s:'think/admin/captcha'
			}, (self, res) => {
				console.log(res)
				self.captcha = res.data.image;
				self.uniqid = res.data.uniqid;
			}); */
			
			uni.request({
				url:this.$service.api.home.captcha,
				method:"GET",
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('token')
				},
				success: (res) => {
					var res = res.data;
					// console.log(res);
					this.captcha = res.data.image;
					this.uniqid = res.data.uniqid;
					}
			});
			
		}
	},
	onShow() {
		this.captchas();
		var ttt = uni.getStorageSync('type');
		
		this.type = ttt
	
	},
	onLoad(op) {
		this.num = op.A;
		this.price = op.c;
		this.money_ss = op.b;
		this.id = op.id;
		var str = uni.getStorageSync("tr")
		
		this.ds_money = Number(this.num * this.money_ss).toFixed(2);
		this.money = Number(this.ds_money * str).toFixed(2);
	}
}
